import { 
	pick,
	rand,
	pluralize,
	shit,
	some,
	shuffle,
} from '@/utils/Rand'

const goal = (id, name, test = () => true) => ({id, name, test})

const BASE_GOALS = [
	goal('bunker', '–í—ã–∂–∏—Ç—å –≤ –±—É–Ω–∫–µ—Ä–µ'),
	goal('reprod', '–î–∞—Ç—å –ø–æ—Ç–æ–º—Å—Ç–≤–æ'),
	goal('survival', '–í—ã–∂–∏—Ç—å –≤ –Ω–æ–≤—ã—Ö —É—Å–ª–æ–≤–∏—è—Ö'),
]

const OPT_GOALS = [
	goal('food', '–û–±–µ—Å–ø–µ—á–∏—Ç—å —Å–µ–±—è –µ–¥–æ–π'),
	goal('water', '–û–±–µ—Å–ø–µ—á–∏—Ç—å —Å–µ–±—è –≤–æ–¥–æ–π'),
	goal('medicine', '–ü–æ–±–µ–¥–∏—Ç—å –±–æ–ª–µ–∑–Ω—å'),
	goal('defence', '–ó–∞—â–∏—Ç–∏—Ç—å —Å–µ–±—è'),
	goal('getout', '–°—É–º–µ—Ç—å –≤—ã–±—Ä–∞—Ç—å—Å—è'),
]

const goals = {}

const set_goals = () => {
	Object
		.keys(goals)
		.forEach(key => delete goals[key])
	const set = [
		...BASE_GOALS,
		...some(OPT_GOALS)
	]
	set.forEach(item => goals[item.id] = item)
}


const stress_fails = (male, female, incidents) => {
	const pop = male + female

	const reason = [
		pop < 2 && '–ñ–∏–∑–Ω—å —Ö–∏–∫–∫–∞–Ω–∞ –æ–∫–∞–∑–∞–ª–∞—Å—å –Ω–µ —Ç–∞–∫–æ–π —Å–ª–∞–¥–∫–æ–π, –∫–∞–∫ –º–æ–∂–Ω–æ –ø–æ–¥—É–º–∞—Ç—å: —Å–µ—Ä—ã–µ —Å—Ç–µ–Ω—ã, –Ω–∏–∑–∫–∏–π –ø–æ—Ç–æ–ª–æ–∫... –û—Ç—Å—É—Ç—Å—Ç–≤–∏–µ –∏–Ω—Ç–µ—Ä–Ω–µ—Ç–∞!',
		pop == 2 && '–ö–∞–∑–∞–ª–æ—Å—å –±—ã, —á—Ç–æ —ç—Ç–∏ –¥–≤–æ–µ –º–æ–≥–ª–∏ –Ω–µ –ø–æ–¥–µ–ª–∏—Ç—å, –∞ –≤–µ–¥—å –Ω–∞—à–ª–æ—Å—å –∂–µ...',
		male == 1 && female == 1 && '–≠—Ç–∏ –¥–≤–æ–µ –æ–∫–∞–∑–∞–ª–∏—Å—å –Ω–µ —Å–∞–º–æ–π –∏–¥–µ–∞–ª—å–Ω–æ–π –ø–∞—Ä–æ–π. –ï–¥–≤–∞ –ª–∏ –º–æ–∂–Ω–æ –Ω–∞–π—Ç–∏ –ø–∞—Ä—É —Ö—É–∂–µ.',
		male < 1 && '–í–æ–∑–º–æ–∂–Ω–æ, –±—É–¥—å —Å—Ä–µ–¥–∏ –Ω–∏—Ö –º—É–∂—á–∏–Ω–∞, —ç—Ç–æ–≥–æ —É–¥–∞–ª–æ—Å—å –±—ã –∏–∑–±–µ–∂–∞—Ç—å.',
		female < 1 && '–î–∞, –Ω–∞ –∫–æ—Ä–∞–±–ª–µ –∂–µ–Ω—â–∏–Ω–∞, –º–æ–∂–µ—Ç, –∏ –∫ –±–µ–¥–µ, –Ω–æ –∑–¥–µ—Å—å –µ—ë —è–≤–Ω–æ –Ω–µ —Ö–≤–∞—Ç–∞–ª–æ.',
		pop > 1 && `–ö—Ç–æ –∂–µ –∑–Ω–∞–ª, —á—Ç–æ —Å—Ä–µ–¥–∏ –≤–∞—Å –æ–∫–∞–∂–µ—Ç—Å—è ${pick(['–º–∞–Ω—å—è–∫', '–ø—Å–∏—Ö', '–±—É–π–Ω—ã–π'])}.`,
		pop > 3 && '–ù–∞—Ä–∫–æ—Ç–∏–∫–∏, –æ—Ä—É–∂–∏–µ, –¥—É—Ä–Ω–∞—è –∫–æ–º–ø–∞–Ω–∏—è –∏ –≤—Å—ë —ç—Ç–æ –≤ —Ç–µ—Å–Ω–æ–º –∑–∞–º–∫–Ω—É—Ç–æ–º –ø–æ–º–µ—â–µ–Ω–∏–∏.',
		pop > 5 && `–ö–∞–∑–∞–ª–æ—Å—å –±—ã –∫–æ–ª–ª–µ–∫—Ç–∏–≤ —Ç–∞–∫–æ–≥–æ —Ä–∞–∑–º–µ—Ä–∞ –¥–æ–ª–∂–µ–Ω —Å–ø—Ä–∞–≤–ª—è—Ç—å—Å—è —Å–æ —Å—Ç—Ä–µ—Å—Å–æ–º –ª—É—á—à–µ. –ù–æ —á–µ–≥–æ-—Ç–æ –≤–∞–º –Ω–µ —Ö–≤–∞—Ç–∏–ª–æ. –ú–æ–∂–µ—Ç, ${pick(['–º—É–∑—ã–∫–∞–Ω—Ç–∞', '—Ç–∞–Ω—Ü–æ–≤—â–∏—Ü—ã', '–∫—Ä–µ–ø–∫–æ–π —Ä—É–∫–∏ –∑–∞–∫–æ–Ω–∞'])}?`,
		incidents > 1 && pop > 1 && '–î–æ–ª–≥–æ –∫—É–∫–æ–≤–∞—Ç—å —Å—Ä–µ–¥–∏ —Ç—Ä—É–ø–æ–≤ –Ω–µ —É–¥–∞–ª–æ—Å—å.',
		incidents > 1 && pop < 2 && '–ù–æ —ç—Ç–∏–º –¥–µ–ª–æ, —É–≤—ã, –Ω–µ –∑–∞–∫–æ–Ω—á–∏–ª–æ—Å—å.',
		incidents > 3 && '–ê –≤–µ–¥—å –≤–∞–º —Å–æ —Å—Ç–æ–ª—å–∫–∏–º–∏ –∏–Ω—Ü–∏–¥–µ–Ω—Ç–∞–º–∏ —É–¥–∞–ª–æ—Å—å —Å–ø—Ä–∞–≤–∏—Ç—å—Å—è',
	].filter(shit)

	return shuffle(reason)
}



export const make_story = () => {
	set_goals()

	const days = rand(2, 8) * 30 + rand(0, 6)
	const days_desc = pluralize(days, `${days} –¥–µ–Ω—å`, `${days} –¥–Ω—è`, `${days} –¥–Ω–µ–π`)

	const male = Math.max(0, rand(1, 3) + rand(0, 2) + rand(0, 3) - 2)
	const female = Math.max(0, rand(1, 3) + rand(0, 2) + rand(0, 3) - 2)
	const popul = male + female

	if (popul < 1) return make_story()

	const male_desc = `${male} ${pluralize(male, '–º—É–∂—á–∏–Ω–∞', '–º—É–∂—á–∏–Ω—ã', '–º—É–∂—á–∏–Ω')}`
	const female_desc = `${female} ${pluralize(female, '–∂–µ–Ω—â–∏–Ω–∞', '–∂–µ–Ω—â–∏–Ω—ã', '–∂–µ–Ω—â–∏–Ω')}`

	const pron_id = pick([
		popul > 1 && 5,
		male < 1 && female < 2 && 2, 
		female < 1 && male < 2 && 1, 
	].filter(shit))

	const no_male = pick([
		' –∏ –Ω–∏ –æ–¥–Ω–æ–≥–æ –º—É–∂—á–∏–Ω—ã', 
		`, –∞ —Å ${pluralize(female, '–Ω–µ—é', '–Ω–∏–º–∏', '–Ω–∏–º–∏')} –Ω–∏ –æ–¥–Ω–æ–≥–æ –º—É–∂—á–∏–Ω—ã`,
		`, –º—É–∂—á–∏–Ω —Å ${pluralize(female, '–Ω–µ—é', '–Ω–∏–º–∏', '–Ω–∏–º–∏')} –Ω–µ –±—ã–ª–æ`,
	])
	const no_female = pick([
		`, –∂–µ–Ω—â–∏–Ω —Å ${pluralize(male, '–Ω–∏–º', '–Ω–∏–º–∏', '–Ω–∏–º–∏')} –Ω–µ –±—ã–ª–æ`,
		' —Å–æ–≤—Å–µ–º –±–µ–∑ –∂–µ–Ω—â–∏–Ω',
		' –∏ –Ω–∏ –æ–¥–Ω–æ–π –∂–µ–Ω—â–∏–Ω—ã',
	])
	const pop_desc = pick([
		male > 0 && female > 0 && `${male_desc} –∏ ${female_desc}`,
		male > 0 && female > 0 && `${female_desc} –∏ ${male_desc}`,
		male < 1 && `${female_desc}${no_male}`,
		female < 1 && `${male_desc}${no_female}`,
	].filter(shit))

	console.log('======================')
	console.log(goals)
	const in_bunker = bunker_story(male, female, days)
	const outside = in_bunker.failed
		? []
		: aftermath(male, female)

	return {
		title: pick([
			'–í–æ—Ç –∏ –≤—Å—ë...',
			'–ñ—Ä–µ–±–∏–π –±—Ä–æ—à–µ–Ω...',
			'–°—Ç–∞–≤–∫–∏ —Å–¥–µ–ª–∞–Ω—ã...',
			'–ß—Ç–æ –∂, –ø–æ—Å–º–æ—Ç—Ä–∏–º...',
		]),

		entry: pick([
			`${popul} ${pluralize(popul, '—á–µ–ª–æ–≤–µ–∫ –≤–æ—à—ë–ª', '—á–µ–ª–æ–≤–µ–∫–∞ –≤–æ—à–ª–∏', '—á–µ–ª–æ–≤–µ–∫ –≤–æ—à–ª–æ')} –≤ –±—É–Ω–∫–µ—Ä: ${pop_desc}.`
		]),

		setup: pick([
			`${pluralize(pron_id, '–ï–º—É', '–ï–π', '–ò–º')} –ø—Ä–µ–¥—Å—Ç–æ—è–ª–æ –ø–µ—Ä–µ–∂–∏—Ç—å ${days_desc} –≤ –±—É–Ω–∫–µ—Ä–µ.`,
			`–í–ø–µ—Ä–µ–¥–∏ —É –Ω${pluralize(pron_id, '–µ–≥–æ', '–µ—ë', '–∏—Ö')} –±—ã–ª–∏ –¥–æ–ª–≥–∏–µ ${days_desc} –≤ –±—É–Ω–∫–µ—Ä–µ.`,
			`–ù–∞ –±–ª–∏–∂–∞–π—à–∏–µ ${days_desc} —ç—Ç–æ—Ç –±—É–Ω–∫–µ—Ä —Å—Ç–∞–Ω–µ—Ç ${pluralize(pron_id, '–µ–≥–æ', '–µ—ë', '–∏—Ö')} –¥–æ–º–æ–º... –∏–ª–∏?..`,
			`–°–ª–µ–¥—É—é—â–∏–µ ${days_desc} ${pluralize(pron_id, '–æ–Ω', '–æ–Ω–∞', '–æ–Ω–∏')} –ø—Ä–æ–≤–µ–¥${pluralize(pron_id, '—ë—Ç', '—ë—Ç', '—É—Ç')} –≤ —ç—Ç–æ–º –±—É–Ω–∫–µ—Ä–µ.`,
		]),

		bunker: in_bunker.story,
		
		aftermath: outside,

		restart: pick([
			'–ò–ª–∏ –Ω–µ—Ç?..',
			'–ê –º–æ–∂–µ—Ç, –≤—Å—ë –±—ã–ª–æ –∏–Ω–∞—á–µ?',
			'–ù–æ –µ—Å—Ç—å –∏ –¥—Ä—É–≥–∏–µ –≤–∞—Ä–∏–∞–Ω—Ç—ã...',
		])
	}
}

const bunker_story = (male, female, days) => {
	const res = {
		story: [],
		failed: false
	}

	const stress_factor = 1 - 0.9 ** Math.abs(male - female)
	const stress_random = rand() * rand()
	const stress_total = 1- (1 - stress_factor) * (1 - stress_random)
	console.log(stress_factor, stress_random, stress_total)

	const {
		morale,
		incidents,
		inc_days
	} = morale_test(days, stress_total)

	console.log(morale, incidents, inc_days)

	switch(true) {
		case morale < 0: {
			res.failed = true
			res.story.push(pick([
				'–û–¥–Ω–∞–∫–æ —Å—Ç—Ä–µ—Å—Å –∂–∏–∑–Ω–∏ –≤ –±—É–Ω–∫–µ—Ä–µ –≤–∑—è–ª —Å–≤–æ—ë'
			]))

			res.story.push(pick([
				`–ü–µ—Ä–≤—ã–π –∏–Ω—Ü–∏–¥–µ–Ω—Ç —Å–ª—É—á–∏–ª—Å—è –Ω–∞ ${inc_days[0]} –¥–µ–Ω—å.`
			]))

			if (incidents > 2) {
				res.story.push(pick([
					`–ó–∞ –Ω–∏–º –ø–æ—Å–ª–µ–¥–æ–≤–∞–ª–∏ –º–Ω–æ–≥–∏–µ –¥—Ä—É–≥–∏–µ.`
				]))
			}

			res.story.push(...stress_fails(male, female, incidents))

			if (incidents > 1) {
				res.story.push(pick([
					`–ù–∞ ${inc_days[inc_days.length - 1]} –¥–µ–Ω—å –≤—Å—ë –±—ã–ª–æ –∫–æ–Ω—á–µ–Ω–æ.`
				]))
			} else {
				res.story.push(pick([
					`–≠—Ç–æ–≥–æ –æ–∫–∞–∑–∞–ª–æ—Å—å –±–æ–ª–µ–µ —á–µ–º –¥–æ—Å—Ç–∞—Ç–æ—á–Ω–æ.`
				]))
			}

			res.story.push(pick([
				`–û –¥–∞–ª—å–Ω–µ–π—à–µ–º –≤—ã–∂–∏–≤–∞–Ω–∏–∏ –º–æ–∂–Ω–æ –∑–∞–±—ã—Ç—å.`
			]))

			break
		}
		case morale < 1: {
			res.failed = false

			res.story.push(pick([
				`–°—Ç–µ–Ω—ã –±—É–Ω–∫–µ—Ä–∞ –∏ –≤–µ—Å—å –Ω–∞–∫–æ–ø–ª–µ–Ω–Ω—ã–π —Å—Ç—Ä–µ—Å—Å –æ—Å—Ç–∞–ª–∏—Å—å –ø–æ–∑–∞–¥–∏.`,
				`–ù–∞–∫–æ–Ω–µ—Ü-—Ç–æ —Å–≤–µ–∂–∏–π –≤–æ–∑–¥—É—Ö... –∏–ª–∏ –Ω–µ –æ—á–µ–Ω—å? –í—Å–µ –∑–ª–æ–∫–ª—é—á–µ–Ω–∏—è –∂–∏–∑–Ω–∏ –≤ –±—É–Ω–∫–µ—Ä–µ –æ—Å—Ç–∞–ª–∏—Å—å –ø–æ–∑–¥–∏.`,
				`–ò—Å–ø—ã—Ç–∞–Ω–∏–µ –æ–∫–∞–∑–∞–ª–æ—Å—å –Ω–µ–ø—Ä–æ—Å—Ç—ã–º, –Ω–æ –≤–ø–æ–ª–Ω–µ –ø—Ä–µ–æ–¥–æ–ª–∏–º—ã–º.`
			]))

			if (incidents > 0) {
				res.story.push(pick([
					`–ò —ç—Ç–æ –Ω–µ —Å–º–æ—Ç—Ä—è –Ω–∞ –Ω–µ–∫–æ—Ç–æ—Ä—ã–µ –∏–Ω—Ü–∏–¥–µ–Ω—Ç—ã.`
				]))
			}

			break
		}
		case morale <= 2: {
			res.failed = false

			res.story.push(pick([
				`–ü—Ñ-—Ñ-—Ñ! –ë—É–Ω–∫–µ—Ä. –î–∞–π—Ç–µ –¥–≤–∞!`,
			]))

			break
		}
	}

	console.log(res)
	return res
}

const morale_test = (days, stress) => {
	const res = {
		morale: 1,
		incidents: 0,
		inc_days: []
	}

	for (let i = 0; i < days; i++) {
		if (res.morale <= 0) return res
		const roll = rand() - stress
		res.morale += roll
		res.morale = Math.min(res.morale, 2)

		const inc = rand() - res.morale
		if (inc > 0) {
			res.inc_days.push(i + 1)
			res.incidents += 1
			const threat = 1 - res.morale
			res.morale -= threat * rand()
		}
	}

	return res
}

const aftermath = (male, female) => {}